.description-details {
  // Position

  // block-model

  // Typography

  // Decoration
  @extend %cursor-pointer;
  // Animation
  @extend %transition-basic;
  
  // Various
  &:hover, &:active {
    @extend %transition-basic;
  }
  
  &:hover {
    @extend %transition-basic;
    color: $color-text-dark;
    background-color: $color-highlight-light;
    border-radius: inherit;
  }

  &:active {
    @extend %transition-basic;
    color: $color-text-dark;
    background-color: $color-highlight-light;
    border-radius: inherit;
  }
  
}